<template>
  <div class="ao-asset-recharge">
    <ao-asset-return></ao-asset-return>
    <div class="content">
      <div class="text" v-if="address">{{ recharge_title }}</div>
      <div class="address" v-if="address">
        <span class="code">{{ address }}</span>
        <span class="btn copy" :data-clipboard-text="address" @click="copy">{{ recharge_btn[0] }}</span>
        <span class="btn" @click="qrcode">{{ recharge_btn[1] }}</span>
      </div>
      <div class="header">
        <div
          class="col"
          style="text-align:center;line-height: 2.4;font-size: 20px;"
        >{{ recharge_text[0] }}</div>
        <div class="col col-5">
          <el-input placeholder type="number" v-model="number"></el-input>
        </div>
        <div class="col col-5">
          <el-button type="primary" @click="recharge">{{ recharge_btn[2] }}</el-button>
        </div>
      </div>
      <div class="header">
        <div
          class="col"
          style="text-align:center;line-height: 2.4;font-size: 20px;"
        >{{ recharge_text[4] }}</div>
        <div class="col col-5">
          <el-input type="text" v-model="certificate" :placeholder="recharge_text[5]"></el-input>
        </div>
        <div class="col col-5"></div>
      </div>
      <div class="header">
        <div class="col"></div>
        <div class="col col-5">
          <el-upload
            :action="upload_img_url"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            list-type="picture-card"
            :data="{key: 'id_card_back'}"
          >
            <img v-if="certificate_url" :src="certificate_url" class="avatar" />
            <i v-else class="el-icon-plus"></i>
          </el-upload>
        </div>
        <div class="col col-5"></div>
      </div>
      <div class="address text">{{ recharge_text[1] }}</div>
      <ul>
        <li class="text">{{ recharge_text[2] }}</li>
        <li class="text">{{ recharge_text[3] }}</li>
      </ul>
    </div>
    <el-dialog :title="recharge_btn[1]" :visible.sync="dialogVisible" width="240px">
      <span slot="footer" class="dialog-footer">
        <div slot="footer" id="qrcode" ref="qrcode"></div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
const upload_img_url = process.env.VUE_APP_BASE_API + "web/uploadImg";
import Clipboard from "clipboard";
import QRCode from "qrcode2";
import AoAssetReturn from "./components/AoAssetReturn";
import { assetRecharge } from "@/api/asset";

var qrcode = null;
export default {
  components: {
    AoAssetReturn
  },
  data() {
    return {
      upload_img_url,
      recharge_title: this.$t("asset.recharge_title"),
      recharge_btn: this.$t("asset.recharge_btn"),
      recharge_text: this.$t("asset.recharge_text"),

      dialogVisible: false,
      address: "",
      certificate: "",
      certificate_url: "",
      number: ""
    };
  },
  mounted() {
    var __data = JSON.parse(localStorage.getItem("jyxt_asset_table_path")); //this.$store.state.__data;
    if (!__data) {
      this.$router.back(-1);
    }
    console.log(__data);
    this.address = __data.address;
    this.currency = __data.currency;
    this.type = __data.type;
  },
  methods: {
    recharge() {
      var data = {
        number: this.number,
        address: this.address,
        currency: this.currency,
        type: this.type,
        certificate_url: this.certificate_url,
        certificate: this.certificate
      };
      assetRecharge(data).then(() => {
        this.$router.back(-1);
      });
    },
    qrcode() {
      if (!qrcode) {
        qrcode = new QRCode("qrcode", {
          width: 200,
          height: 200,
          text: this.address
        });
      }
      this.dialogVisible = true;
    },
    copy() {
      var clipboard = new Clipboard(".copy");
      clipboard.on("success", e => {
        this.$message({ message: "å¤å¶æå", type: "success" });
        // éæ¾åå­
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        // ä¸æ¯æå¤å¶
        this.$message({ message: "è¯¥æµè§å¨ä¸æ¯æèªå¨å¤å¶", type: "error" });
        // éæ¾åå­
        clipboard.destroy();
      });
    },
    handleAvatarSuccess(res, file) {
      if (res.message) {
        this.$message({
          message: res.message,
          type: res.code === 0 ? "success" : "error"
        });
      }
      if (res.code === 0) {
        this.certificate_url = res.data.url;
      }
    }
  }
};
</script>
<style lang="scss">
.el-dialog__body {
  padding: 0;
}
</style>

<style scoped lang="scss">
.ao-asset-recharge {
  .header {
    color: #c7cce6;
    display: flex;
  }
  .col {
    flex: 1;
    & + .col {
      margin-left: 20px;
    }
    & > div {
      font-size: 12px;
      color: #c7cce6;
      margin-bottom: 10px;
    }
    &.col-5 {
      flex: 4;
    }
    .el-input .el-input__inner {
      padding: 10px;
      border: 1px solid #4e5b85;
      font-size: 18px;
      color: #c7cce6;
      border-radius: 3px;
      // background-color: transparent;
      outline: none;
      font-size: 14px;
    }
  }
  .content {
    width: 100%;
    padding: 20px;
  }
  .text {
    color: #637085;
    font-size: 12px;
  }
  .address {
    margin: 50px auto;
    span {
      color: #c7cce6;
      font-size: 18px;
      &.code.bg {
        background: #2b3c71;
      }
      &.btn {
        margin: 0 30px;
        font-size: 14px;
        color: #5697f4;
        cursor: pointer;
      }
    }
  }
  ul li {
    list-style: disc inside;
  }
  .avatar {
    height: 146px;
    width: 146px;
    border-radius: 6px;
  }
}
</style>



